<template>
  <div>
    <li @mouseenter="tabindex(index)" v-cloak :class="{line:index===currentIndex}">
      <a :class="{def_line:index===len}">{{tit.name}}</a>
    </li>
  </div>
</template>
<script>
export default {
  name: 'dynamicNav',
  props: {
    tit: Object,
    tab: Function,
    index: Number,
    currentIndex: Number,
    len: Number
  },
  methods: {
    tabindex (i) {
      this.tab(i)
    }
  }
}
</script>

<style scoped lang="stylus">
  .def_line
    border-right none!important
  .line
    background red
    color white
  li
    width 100%
    cursor pointer
    line-height 50px
    height 50px
    font-size 18px
    box-sizing border-box
    background white
    color red
    a
      width 100%
      display block
      text-align center
      float left
      height 40px
      line-height 40px
      margin-top 5px
      box-sizing border-box
      border-right 1px solid pink
</style>



