<template>
  <div class="personnel_work_all clear-fix">
    <ul @mouseleave="leaveul" class="personnel_work_ul">
      <li v-for="(con,index) in item.contents" :key="index" @mouseenter="itemli(index)" :class="{activeli:index===currentIndex}">
        <el-link :underline="false" class="least-list" tag="div">
          <img :src="con.src" class="star">
          <span class="hot-title text_ell">{{con.value}}</span>
        </el-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'personnel_group',
  data () {
    return {
      currentIndex: null,
      tagIndex: 0
    }
  },
  props: {
    item: Object
  },
  methods: {
    itemli (i) {
      this.currentIndex = i
    },
    leaveul () {
      this.currentIndex = 100
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    background #fff3f3
  .personnel_work_all
    width 100%
    .personnel_work_ul
      width 100%
      float left
      .least-list
        display block
        height 45px
        margin-left 20px
        line-height: 45px
        &:hover
          color red
        .star
          float left
          margin-top 16px
          margin-left 20px
        .hot-title
          width 60%
          float left
          margin-left 20px
          font-size 16px
</style>



