<template>
    <div>
      <ul @mouseleave="leaveul">
        <li v-for="(con,index) in item.contents" :key="index" @mouseenter="itemli(index)" :class="{activeli:index==currentIndex}">
          <el-link :underline="false" class="least-list" tag="div">
            <span class="dot"></span>
            <span class="hot-title text_ell">{{con.value}}</span>
            <span class="hot-type">{{con.leastType}}</span>
          </el-link>
        </li>
      </ul>
    </div>
</template>

<script>
export default {
  name: 'itemul',
  data () {
    return {
      currentIndex: 100
    }
  },
  props: {
    item: Object
  },
  methods: {
    itemli (i) {
      this.currentIndex = i
    },
    leaveul () {
      this.currentIndex = 100
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    background pink
  .least-list
    display block
    width 100%
    height 45px
    line-height: 45px
    .dot
      float left
      width 7px
      height 7px
      background gray
      border-radius 50%
      margin-top 16px
      margin-left 20px
    .hot-type
      margin-right 20px
      float right
    .hot-title
      width 30%
      float left
      margin-left 20px
</style>



