import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import CA from '@/views/currentAffairs/CA.vue'
import CW from '@/views/cadreWork/CW.vue'
import SB from '@/views/selfBuild/SB.vue'
import TW from '@/views/talentWork/TW.vue'
import SW from '@/views/serviceWindow/SW.vue'
import SG from '@/views/studyGarden/SG.vue'
import PW from '@/views/partyWork/PW.vue'
import SL from '@/views/abandonedPages/SecondaryLeft.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/CA',
      name: 'CA',
      component: CA
    },
    {
      path: '/SG',
      name: 'SG',
      component: SG
    },
    {
      path: '/CW',
      name: 'CW',
      component: CW
    },
    {
      path: '/SB',
      name: 'SB',
      component: SB
    },
    {
      path: '/TW',
      name: 'TW',
      component: TW
    },
    {
      path: '/SW',
      name: 'SW',
      component: SW
    },
    {
      path: '/PW',
      name: 'PW',
      component: PW
    },
    {
      path: '/SL',
      name: 'SL',
      component: SL
    }
  ]
})



