<template>
  <div>
    <ul @mouseleave="leaveul">
      <li v-for="(con,index1) in item.contents" :key="index1" @mouseenter="itemli(index1)" :class="{bg_color:activeIndex==index1}">
        <router-link :to='con.path' class="least-list">
          <span class="dot" :class="{point_red:index1===activeIndex}"></span>
          <span class="hot-title text_ell" :class="{word_red:index1===activeIndex}">{{con.value}}</span>
          <span class="hot-date text_ell" :class="{word_red:index1===activeIndex}">{{con.leastType}}</span>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'dynamicGroup',
  data () {
    return {
      activeIndex: null
    }
  },
  props: {
    item: Object
  },
  methods: {
    tabindex (index) {
      this.handler(index)
    },
    itemli (i) {
      this.activeIndex = i
    },
    leaveul () {
      this.activeIndex = 100
    }
  }
}
</script>

<style scoped lang="stylus">
  .point_red
    background red !important
   .word_red
    color red !important
  .bg_color
    background pink
  li
    width 100%
    box-sizing border-box
    line-height 48px
    height 48px
    .least-list
      display block
      width 100%
      height 48px
      line-height 48px
      .dot
        float left
        width 7px
        height 7px
        background #808080
        border-radius 50%
        margin-top 20px
        margin-left 10px
      .hot-title
        width 60%
        float left
        color #808080
        margin-left 20px
      .hot-date
        float right
        margin-right 30px
        color #808080
</style>



