<template>
  <li  class="item_li" @mouseenter="friendHand(index)"  v-cloak>
    <a :underline="false" class="name" :class="{active:index===friendIndex}">{{title.name}}</a>
  </li>
</template>
<script>
export default {
  name: 'friendNav',
  data () {
    return {
    }
  },
  props: {
    title: Object,
    friend_handler: Function,
    index: Number,
    friendIndex: Number
  },
  methods: {
    friendHand (index) {
      this.friend_handler(index)
    }
  }
}
</script>
<style  lang="stylus" scoped>
  .active
    color red
    background #ffff
  [v-cloak]
    display none
  .item_li
    float left
    line-height 50px
    .name
      text-align center
      padding 0 20px
      line-height 45px
      border-top 5px solid red
      display block
</style>



