<template>
  <li  class="item_li" @mouseenter="libraryHand(index)"  v-cloak>
    <a :underline="false" class="name" :class="{active:index===libraryIndex}">{{title.name}}</a>
  </li>
</template>
<script>
export default {
  name: 'libraryNav',
  data () {
    return {
    }
  },
  props: {
    title: Object,
    library_handler: Function,
    index: Number,
    libraryIndex: Number
  },
  methods: {
    libraryHand (index) {
      this.library_handler(index)
    }
  }
}
</script>
<style  lang="stylus" scoped>
  .active
    font-style italic
    color orange
    border-bottom 2px solid orange
  [v-cloak]
    display none
  .item_li
    float left
    line-height 50px
    .name
      text-align center
      font-weight 700
      cursor pointer
      padding 10px 0
      margin  0 15px
</style>



