<template>
  <div class="self_build_all clear-fix">
    <div class="self_build_img">
      <router-link to="/CA">
        <img src="@/assets/images/index_11.png">
      </router-link>
      <router-link to="/SG">
        <img src="@/assets/images/index_11.png">
      </router-link>
      <router-link to="/CW">
        <img src="@/assets/images/index_11.png">
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'self_build',
  data () {
    return {
      currentIndex: null,
      tagIndex: 0
    }
  },
  props: {
    item: Object,
    cadreInfo: Array
  },
  methods: {
    itemli (i) {
      this.currentIndex = i
    },
    leaveul () {
      this.currentIndex = null
    },
    tagGroup (i) {
      this.tagIndex = i
    },
    leavetag () {
      this.tagIndex = 0
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    background #fff3f3
  .tagli
    background yellow
    cursor pointer
    border 1px solid yellow
    color red
  .self_build_all
    width 100%
    .self_build_img
      float left
      img
        width 85%
        height 100px
        margin 2px 0 5px 0
        border-radius 15px 15px
</style>



