<template>
  <div class="">
    <div class="date-search-box">
      <div class="date-search d-flex spaceBetween">
        <div class="date-box">
          <span>党建工作&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;</span>
          <current-date></current-date>
        </div>
        <div class="search-box">
          <el-autocomplete
            class="input-with-select"
            v-model="hotSearch"
            :trigger-on-focus="false"
            placeholder="请输入关键词"
            @select="handleSelect">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-autocomplete>
        </div>
      </div>
    </div>
    <div class="pw-content-box">
      <div class="current-location-box">
        <span class="pw-line"></span>
        <span class="pw-txt">您当前的位置：</span>
        <router-link :to="{ path: '/' }" class="pw-link"><i class="el-icon-s-home"></i>首页</router-link>
        <span>&nbsp;>&nbsp;党建工作&nbsp;>&nbsp;</span>
        <span class="current-location-name">{{locationName}}</span>
      </div>
      <div class="PW-tabs-box">
        <ul class="pw-ul clear-fix">
          <li v-for="(item,index) in tabLi" :key="index" class="pw-tab">
            <span class="pw-tab-title" :class="{active_bg:(index+1)==activeIndex}" @click="handleClick(index)">{{item.title}}</span>
            <span class="pw-tab-line"></span>
          </li>
        </ul>
        <ul class="pw-ul-small clear-fix">
          <li v-for="(item,index) in tagLi" :key="index" class="pw-tab" v-show="index===activeIndex">
            <span class="pw-tab-title" :class="{active_bg:(index+1)==linkIndex}" @click="handleClick(index)" v-for="(text,index) in item.title" :key="text.id">{{text.name}}</span>
          </li>
        </ul>
        <ul v-for="(item,index) in newsAll" :key="item.id" class="ul-con" v-show="index===linkIndex">
          <li v-for="(itemLi,index) in item.newsList" :key="itemLi.id" class="li-con">
            <img :src="itemLi.img" class="li-img">
            <div class="li-word">
              <div class="li-title">
                <router-link  class="li-router" :to='itemLi.path' :class="{active_link:index===changeIndex}" @mouseenter="clickHandler(index1)">{{itemLi.title}}
                </router-link>
                <span>{{itemLi.date}}</span>
              </div>
              <p class="li-tag font-16">{{itemLi.con}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <el-pagination
      background
      class="PW-pagination"
      layout="prev, pager, next"
      prev-text="上一页"
      next-text="下一页"
      :total="1000">
    </el-pagination>
  </div>
</template>
<script>
import SecondaryNews from '../components/SecondaryNews'
import CurrentDate from '../components/CurrentDate'
export default {
  name: 'PW-component',
  components: {
    SecondaryNews,
    CurrentDate
  },
  data () {
    return {
      hotSearch: '',
      activeIndex: 0,
      changeIndex: 100,
      linkIndex: 0,
      locationName: '基层党建',
      tabLi: [
        {
          id: '1',
          title: '基层党建'
        },
        {
          id: '2',
          title: '党员队伍建设'
        }
      ],
      tagLi: [
        {
          id: '1',
          title: [
            {
              id: '1',
              name: '城市基层党建'
            },
            {
              id: '2',
              name: '农村党建'
            },
            {
              id: '3',
              name: '机关党建'
            },
            {
              id: '4',
              name: '国企党建'
            },
            {
              id: '5',
              name: '非公经济组织党建'
            },
            {
              id: '6',
              name: '社会组织党建'
            }
          ]
        },
        {
          id: '2',
          title: [
            {
              id: '1',
              name: '党员文摘'
            },
            {
              id: '2',
              name: '学习心得'
            },
            {
              id: '3',
              name: '机关党建'
            },
            {
              id: '4',
              name: '国企党建'
            }
          ]
        }
      ],
      newsAll: [
        {
          id: '1',
          newsList: [
            {
              id: '1-1',
              img: require('@/assets/images/index_08.png'),
              title: '创新"三会一课"形式00',
              con: '"三会一课"制度是我们党注重思想建党，严密党的组织，增强党的创造力凝聚力战斗力的鲜明体现和制度安排。',
              date: '2019-7-31',
              path: '/CW'
            },
            {
              id: '1-2',
              img: require('@/assets/images/index_08.png'),
              title: '创新"三会一课"形式00',
              con: '"三会一课"制度是我们党注重思想建党，严密党的组织，增强党的创造力凝聚力战斗力的鲜明体现和制度安排。',
              date: '2019-7-31',
              path: '/SG'
            }
          ]
        },
        {
          id: '2',
          newsList: [
            {
              id: '2-1',
              img: require('@/assets/images/index_08.png'),
              title: '省委组织部部位会(扩大)举行主题教育第三次集中学习',
              con: '7月23日至24日省委组织部部位会(扩大)举行"不忘初心，牢记使命"主题教育第三次集中学习,采取集中自学与交流研讨相结合的方式进行......',
              date: '2019-7-31',
              path: '/CW'
            },
            {
              id: '2-2',
              img: require('@/assets/images/index_08.png'),
              title: '创新"三会一课"11',
              con: '"三会一课"制度是我们党注重思想建党，严密党的组织，增强党的创造力凝聚力战斗力的鲜明体现和制度安排。',
              date: '2019-7-31',
              path: '/SG'
            }
          ]
        }
      ]
    }
  },
  watch: {
    $route: {
      handler (val, oldVal) {
        const {query} = val
        this.activeIndex = query.id
        this.locationName = query.title
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted () {
    this.getRouterData()
    console.log(this.activeIndex)
  },
  methods: {
    handleSelect (item) {
      console.log(item)
    },
    handleClick (i) {
      this.activeIndex = i
      this.locationName = this.tabLi[i].title
      this.$router.push({name: 'PW', query: {id: this.activeIndex + 1, title: this.locationName}})
      console.log(this.activeIndex)
    },
    clickHandler (i) {
      this.changeIndex = i
      console.log(this.changeIndex)
      console.log(i)
    },
    getRouterData () {
      this.activeIndex = this.$route.query.id - 1
      this.locationName = this.$route.query.title
    }
  }
}
</script>

<style lang="stylus" scoped>
.active_link
  color red !important
.active_bg
  border-radius 5px 5px
  background red
  color white
.font-16
  font-size 16px
.date-search-box
  width 100%
  height 60px
  border-bottom 1px solid rgba(205,201,201,0.9)
  line-height 60px
  .date-search
    width 1200px
    height 100%
    margin 0 auto
    .date-box
      width auto
      height 100%
      font-size 18px
    .search-box
      height 28px
      width 330px
      .input-with-select
        width 100%
        height 28px !important
      .el-icon-search
        width 34px
        height 28px
.pw-content-box
  width 1200px
  height auto
  margin 20px auto
  .current-location-box
    width 100%
    height 30px
    line-height 30px
    span
      font-size 18px
    .pw-line
      width 4px
      height 30px
      background red
      float left
    .pw-txt
      float left
      margin-left 15px
    .pw-link
      text-decoration none
      font-size 18px
    .el-icon-s-home
      margin-right 5px
      text-decoration none
    .current-location-name
      color red
  .PW-tabs-box
    width 100%
    margin-top 25px
    .pw-ul
      height 25px
      line-height 25px
      border-bottom 1px solid gray
      padding-left 10px
      padding-bottom 25px
      .pw-tab
        float left
        height auto
        &:last-child .pw-tab-line
          display none
        .pw-tab-title
          float left
          padding 6px 17px
          margin 0 5px
          cursor pointer
          font-size 18px
        .pw-tab-line
          float left
          width 1px
          height 14px
          background gray
          margin 11px 10px
    .pw-ul-small
      height 25px
      line-height 25px
      margin-left 10px
      margin-top 15px
      .pw-tab
        float left
        height auto
        .pw-tab-title
          float left
          padding 6px 17px
          margin 0 5px
          cursor pointer
          font-size 18px
    .ul-con
      margin-top 10px
      .li-con
        height 140px
        border-bottom 1px solid gray
        padding 15px 0
        .li-img
          width 300px
          height 150px
          border-radius 10px 10px
          float left
        .li-word
          float left
          margin-left 40px
          margin-top 5px
          width 860px
          .li-title>span
            float right
          .li-title>.li-router
            color black
            font-weight bold
            font-size 20px
          .li-title>.li-router:hover
            color red
          .li-tag
            width 640px
            color gray
.PW-tabs-box .active_link
  color red
.PW-pagination
  width 1200px
  height auto
  overflow hidden
  text-align right
  margin 40px auto 30px
</style>



