<template>
  <ul @mouseleave="leaveul" class="library_work_ul d-flex">
    <li v-for="(con,index) in item.contents" :key="index" @mouseenter="hoverli(index)">
      <el-link :underline="false" tag="div">
        <img :src="con.src" class="star">
        <span class="hot-title text_ell" :class="{activeli:index===currentIndex}">{{con.value}}</span>
      </el-link>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'library_group',
  data () {
    return {
      currentIndex: 0
    }
  },
  props: {
    item: Object
  },
  methods: {
    leaveul () {
      this.currentIndex = null
    },
    hoverli (i) {
      this.currentIndex = i
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    color red!important
  .library_work_ul
    width 100%
    padding 30px 0
    li
      flex 1
      text-align center
      .star
        width 200px
        height 250px
        margin-bottom 5px
      .hot-title
        display block
        margin 0 auto
        width 85%
</style>



