<template>
  <li class="focus-news-list">
    <a href="javascript:;">
      <img class="news-con-img" :src="data.img" alt="">
      <div class="news-con-box">
        <span class="news-con-title">{{data.title}}</span>
        <p class="news-con-con line-clamp2 font-12">
          {{data.con}}
        </p>
      </div>
      <span class="news-con-date font-12">{{data.date}}</span>
    </a>
  </li>
</template>
<script>
export default {
  name: 'secondary-news-component',
  props: {
    data: {
      type: Object,
      default () {
        return ''
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.focus-news-list
  width 100%
  height auto
  border-bottom 1px solid #cccccc
  padding 10px 0
  a
    display block
    width 100%
    height auto
    overflow hidden
    .news-con-img
      width 300px
      height 150px
      float left
      margin-right 20px
    .news-con-box
      width 770px
      height 150px
      float left
      &:hover .news-con-title
        color #CE8077
      .news-con-title
        font-size 16px
        color #5C5C5C
        font-weight 500
        margin-top 10px
      .news-con-con
        color #A1A1A1
        margin-top 10px
    .news-con-date
      float right
      color #AEAEAE
</style>



