<template>
  <li class="item_li" @mouseenter="countryHand(index)"  v-cloak>
    <a :underline="false" class="name" :class="{active:index===countryIndex}">{{title.name}}</a>
  </li>
</template>
<script>
export default {
  name: 'countryNav',
  data () {
    return {
    }
  },
  props: {
    title: Object,
    country_handler: Function,
    index: Number,
    countryIndex: Number
  },
  methods: {
    countryHand (index) {
      this.country_handler(index)
    }
  }
}
</script>
<style  lang="stylus" scoped>
  .active
    color red
    border-bottom 3px solid red
  [v-cloak]
    display none
  .item_li
    line-height 50px
    flex 1
    text-align center
    cursor pointer
    .name
      padding 14px 0
      margin  0 15px
      font-size 17px
      font-weight bold
</style>



