<template>
  <div>
    <li  class="item_li" @mouseenter="personnelHand(index)"  v-cloak>
      <a :underline="false" class="name" :class="{active:index===personnelIndex}">{{title.name}}</a>
    </li>
  </div>
</template>
<script>
export default {
  name: 'personnel',
  data () {
    return {
    }
  },
  props: {
    title: Object,
    personnel_handler: Function,
    index: Number,
    personnelIndex: Number
  },
  methods: {
    personnelHand (index) {
      this.personnel_handler(index)
    }
  }
}
</script>
<style  lang="stylus" scoped>
  .active
    font-style italic
    color orange
    border-bottom 2px solid orange
  [v-cloak]
    display none
  .item_li
    float left
    line-height 50px
  .name
    text-align center
    font-weight 700
    cursor pointer
    padding 10px 0
    margin  0 15px
</style>



