<template>
  <div>
      <li  class="item_li" @mouseenter="partyHand(index)"  v-cloak>
        <a :underline="false" class="name" :class="{active:index===partyIndex}">{{title.name}}</a>
      </li>
  </div>
</template>
<script>
export default {
  name: 'PartyNav',
  data () {
    return {
    }
  },
  props: {
    title: Object,
    index: Number,
    party_handler: Function,
    partyIndex: Number
  },
  methods: {
    partyHand (i) {
      this.party_handler(i)
    }
  }
}
</script>
<style  lang="stylus" scoped>
  .active
    font-style italic
    color orange
    border-bottom 2px solid orange
  [v-cloak]
    display none
  .item_li
    float left
    line-height 50px
  .name
    text-align center
    font-weight 700
    cursor pointer
    padding 10px 0
    margin  0 15px
</style>



