<template>
  <div class="cadre_work_all clear-fix">
    <ul @mouseleave="leaveul" class="cadre_work_ul">
      <li v-for="(con,index) in item.contents" :key="index" @mouseenter="itemli(index)" :class="{activeli:index===currentIndex}">
        <el-link :underline="false" class="least-list" tag="div">
          <img :src="con.src" class="star">
          <span class="hot-title text_ell">{{con.value}}</span>
        </el-link>
      </li>
    </ul>
    <ul class="cadre_work_tag" @mouseleave="leavetag">
      <li v-for="(item,index) in cadreInfo" :key="index" @mouseenter="tagGroup(index)" :class="{tagli:index===tagIndex}">
        <img :src="item.src" width="10%" class="cadre_work_img" alt="">
        <div class="cadre_work_word">
          <div>{{item.name}}</div>
          <span class="introduce">{{item.introduce}}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'cadre_group',
  data () {
    return {
      currentIndex: null,
      tagIndex: 0
    }
  },
  props: {
    item: Object,
    cadreInfo: Array
  },
  methods: {
    itemli (i) {
      this.currentIndex = i
    },
    leaveul () {
      this.currentIndex = null
    },
    tagGroup (i) {
      this.tagIndex = i
    },
    leavetag () {
      this.tagIndex = 0
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    background #fff3f3
  .tagli
    background-image linear-gradient(to bottom ,#fffcc7,#FFFF00)
    cursor pointer
    border 1px solid yellow
    color red
  .cadre_work_all
    width 100%
    .cadre_work_ul
      width 62%
      float left
      .least-list
        display block
        width 100%
        height 45px
        margin-left 20px
        line-height: 45px
        &:hover
          color red
        .star
          float left
          margin-top 16px
          margin-left 20px
        .hot-title
          float left
          margin-left 20px
          font-size 16px
    .cadre_work_tag
      width 28%
      margin-right 30px
      float right
      li
        width 90%
        height 50px
        margin-bottom 10px
        border-radius 15px 15px
        border 1px solid red
        .cadre_work_img
          float left
          margin 10px
        .cadre_work_word
          float left
          div
            margin-top 5px
            font-weight bold
          .introduce
            color #808080
            font-size 13px
</style>



