<template>
  <div id="app">
    <el-container>
      <el-header width="100%" height="auto">
        <header-page></header-page>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
      <el-footer width="100%" height="291">
        <footer-page></footer-page>
      </el-footer>
    </el-container>
  </div>
</template>
<script>
import HeaderPage from '@/layout/Header'
import FooterPage from '@/layout/Footer'
export default {
  name: 'App',
  components: {
    HeaderPage,
    FooterPage
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.el-header, .el-footer {
  text-align: center;
  padding: 0;
}
.el-main {
  width: 100%;
  color: #565656;
  text-align: left;
  margin: 0 auto;
  padding: 0!important;
  overflow: hidden;
}
</style>



