<template>
  <div>
    <div class="party_work_all clear-fix">
      <ul @mouseleave="leaveul" class="party_work_ul">
        <li v-for="(con,index) in item.contents" :key="index" @mouseenter="itemli(index)" :class="{activeli:index===currentIndex}">
          <el-link :underline="false" class="least-list" tag="div">
            <img :src="con.src" class="star">
            <span class="hot-title text_ell">{{con.value}}</span>
          </el-link>
        </li>
      </ul>
      <div class="party_work_img">
        <router-link to="/CA">
          <img src="@/assets/images/index_11.png">
        </router-link>
        <router-link to="/SG">
          <img src="@/assets/images/index_11.png">
        </router-link>
        <router-link to="/CW">
          <img src="@/assets/images/index_11.png">
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'party_work',
  data () {
    return {
      currentIndex: null
    }
  },
  props: {
    item: Object
  },
  methods: {
    itemli (i) {
      this.currentIndex = i
    },
    leaveul () {
      this.currentIndex = 100
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    background #fff3f3
  .party_work_all
    width 100%
    .party_work_ul
      width 62%
      float left
      .least-list
        display block
        width 100%
        height 45px
        margin-left 20px
        line-height: 45px
        &:hover
          color red
        .star
          float left
          margin-top 16px
          margin-left 20px
        .hot-title
          width 60%
          float left
          margin-left 20px
          font-size 16px
    .party_work_img
      width 28%
      float right
      margin-right 30px
      img
        width 300px
        height 100px
        border-radius 15px 15px
</style>



