<template>
  <span>{{nowTime}} 星期{{week}}</span>
</template>
<script>
export default {
  name: 'current-date-component',
  data () {
    return {
      nowTime: '',
      week: ''
    }
  },
  created () {
    this.timeFormate(new Date())
  },
  methods: {
    timeFormate (timeStamp) {
      let year = new Date(timeStamp).getFullYear()
      let month = new Date(timeStamp).getMonth() + 1 < 10 ? '0' + (new Date(timeStamp).getMonth() + 1) : new Date(timeStamp).getMonth() + 1
      let date = new Date(timeStamp).getDate() < 10 ? '0' + new Date(timeStamp).getDate() : new Date(timeStamp).getDate()
      let day = new Date(timeStamp).getDay()
      switch (day) {
        case 1 :
          this.week = '一'
          break
        case 2 :
          this.week = '二'
          break
        case 3 :
          this.week = '三'
          break
        case 4 :
          this.week = '四'
          break
        case 5 :
          this.week = '五'
          break
        case 6 :
          this.week = '六'
          break
        case 0 :
          this.week = '日'
          break
      }
      this.nowTime = year + '年' + month + '月' + date + '日'
    }
  }
}
</script>



