<template>
  <div class="party_home_con clear-fix">
    <ul class="party_home_img">
      <li v-for="(text,index) in item.image" :key="index">
        <img :src="text.src" :class="{activeimg:index===0}">
      </li>
    </ul>
    <ul @mouseleave="leaveul" class="party_home_ul">
      <li v-for="(con,index) in item.contents" :key="index" @mouseenter="hoverli(index)" :class="{activeli:index===currentIndex}">
        <el-link :underline="false" class="least-list" tag="div">
          <i class="point" :class="{point_red:index===currentIndex}"></i>
          <span class="hot-title text_ell" :class="{word_red:index===currentIndex}">{{con.value}}</span>
          <span class="hot-date text_ell" :class="{word_red:index===currentIndex}">{{con.leastType}}</span>
        </el-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'party_home',
  data () {
    return {
      currentIndex: null
    }
  },
  props: {
    item: Object
  },
  methods: {
    hoverli (i) {
      this.currentIndex = i
    },
    leaveul () {
      this.currentIndex = 100
    }
  }
}
</script>

<style scoped lang="stylus">
  .activeli
    background #fff3f3
  .point_red
    background red!important
  .word_red
    color red!important
  .party_home_con
    width 100%
    margin-top 10px
    .party_home_img
      float left
      margin-left 10px
      img
        width 230px
        height 135px
      .activeimg
        margin-bottom 10px
    .party_home_ul
      width 78%
      float right
      .least-list
        display block
        height 47px
        margin-left 20px
        line-height: 47px
        .point
          width 5px
          height 5px
          background black
          border-radius 50%
          float left
          margin-top 20px
        .hot-title
          width 40%
          float left
          margin-left 20px
          font-size 16px
        .hot-date
          float right
          margin-right 20px
          color #808080
</style>



